import React, { useEffect, useState } from 'react';
import Master from '../components/Master';

const getAudio = source => {
  if (typeof window !== 'undefined') {
    const audio = new Audio(source);
    return audio;
  }
};

const Home = () => {
  return (
    <>
      <h1>Hello Next.js!</h1>
      <Master />
    </>
  );
};

export default Home;
